body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.link {
  color: #fff;
  background-color: #db7093;
  border: none;
  border-radius: 5px;
  margin: 15px 0 0 15px;
  padding: 10px 15px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.link:hover {
  color: #000;
  background-color: #00f;
}

.form {
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

.form-input {
  border: 1px solid #29abd3;
  border-radius: 5px 0 0 5px;
  padding: 10px 15px;
  font-size: 16px;
}

.form-input-submite {
  cursor: pointer;
  color: #fff;
  background-color: #29abd3;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 11px 15px;
  font-size: 16px;
}

.form-input-submite:hover {
  background-color: #295fd3;
  box-shadow: 42px 12px 107px 3px rgba(0, 0, 0, .77);
}

.article-wrapper {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;
  display: flex;
}

.articles-card {
  box-sizing: border-box;
  width: calc(25% - 30px);
  border: 1px solid #29abd3;
  border-radius: 5px;
  margin: 0 15px 15px 0;
  padding: 5px;
}

.article-img {
  width: 100%;
  max-height: 300px;
  border-radius: 5px;
}

.article-link-conteiner {
  justify-content: center;
  display: flex;
}

.article-link {
  color: #fff;
  background-color: #00f;
  border-radius: 5px;
  padding: 5px 7px;
  text-decoration: none;
}

.article-error {
  color: red;
  font-size: 20px;
}

.load-more-btn {
  cursor: pointer;
  color: #fff;
  background-color: #29abd3;
  border: none;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  padding: 11px 15px;
  font-size: 16px;
  display: block;
}

.load-more-btn:disabled {
  opacity: .5;
  cursor: not-allowed;
}

.load-more-btn:hover {
  background-color: #295fd3;
  box-shadow: 42px 12px 107px 3px rgba(0, 0, 0, .77);
}

.hidden {
  display: none;
}

